/* purgecss start ignore */

@charset "UTF-8";

.szh-menu-container {
  position: relative;
  width: 0px;
  height: 0px;
}

.szh-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  width: -webkit-max-content;
  width: max-content;
  position: absolute;
  z-index: 100;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.szh-menu:focus {
  outline: none;
}

.szh-menu--state-closed {
  display: none;
}

.szh-menu__arrow {
  box-sizing: border-box;
  width: 0.75rem;
  height: 0.75rem;
  background-color: #fff;
  border: 1px solid transparent;
  border-left-color: rgba(0, 0, 0, 0.1);
  border-top-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: -1;
}

.szh-menu__arrow--dir-left {
  right: -0.375rem;
  transform: translateY(-50%) rotate(135deg);
}

.szh-menu__arrow--dir-right {
  left: -0.375rem;
  transform: translateY(-50%) rotate(-45deg);
}

.szh-menu__arrow--dir-top {
  bottom: -0.375rem;
  transform: translateX(-50%) rotate(-135deg);
}

.szh-menu__arrow--dir-bottom {
  top: -0.375rem;
  transform: translateX(-50%) rotate(45deg);
}

.szh-menu__item {
  cursor: pointer;
}

.szh-menu__item:focus {
  outline: none;
}

.szh-menu__item--hover {
  background-color: #ebebeb;
}

.szh-menu__item--focusable {
  cursor: default;
  background-color: inherit;
}

.szh-menu__item--disabled {
  cursor: default;
  color: #aaa;
}

.szh-menu__submenu {
  position: relative;
}

.szh-menu__group {
  box-sizing: border-box;
}

.szh-menu__radio-group {
  margin: 0;
  padding: 0;
  list-style: none;
}

.szh-menu__divider {
  height: 1px;
  margin: 0.5rem 0;
  background-color: rgba(0, 0, 0, 0.12);
}

.szh-menu-button {
  box-sizing: border-box;
}

.szh-menu {
  -webkit-user-select: none;
          user-select: none;
  color: #212529;
  border: none;
  border-radius: 0.25rem;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.133), 0 0.6px 2px rgba(0, 0, 0, 0.1);
  min-width: 10rem;
  padding: 0.5rem 0;
}

.szh-menu__item {
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.375rem 1.5rem;
}

.szh-menu-container--itemTransition .szh-menu__item {
  transition-property: background-color, color;
  transition-duration: 0.15s;
  transition-timing-function: ease-in-out;
}

.szh-menu__item--active {
  color: #fff;
  background-color: #007bff;
}

.szh-menu__item--type-radio {
  padding-left: 2.2rem;
}

.szh-menu__item--type-radio::before {
  content: "○";
  position: absolute;
  left: 0.8rem;
  top: 0.55rem;
  font-size: 0.8rem;
}

.szh-menu__item--type-radio.szh-menu__item--checked::before {
  content: "●";
}

.szh-menu__item--type-checkbox {
  padding-left: 2.2rem;
}

.szh-menu__item--type-checkbox::before {
  position: absolute;
  left: 0.8rem;
}

.szh-menu__item--type-checkbox.szh-menu__item--checked::before {
  content: "✔";
}

.szh-menu__submenu > .szh-menu__item {
  padding-right: 2.5rem;
}

.szh-menu__submenu > .szh-menu__item::after {
  content: "❯";
  position: absolute;
  right: 1rem;
}

.szh-menu__header {
  color: #888;
  font-size: 0.8em;
  padding: 0.2rem 1.5rem;
  text-transform: uppercase;
}

.lc-menu .rc-menu__item {
    display: flex;
    justify-content: space-between;
    padding: 12px 16px 12px 12px;
    font-family: var(--lc-font-family-sans);
  }

.lc-menu .rc-menu__item-button-wrapper {
      display: inline-block;
    }

.lc-menu .rc-menu__item--anchor {
      position: relative;
      color: inherit;
      text-decoration: none;
    }

.lc-menu .rc-menu__item--anchor[target='_blank']::after {
        position: absolute;
        right: 12px;
        content: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAQElEQVR42qXKwQkAIAxDUUdxtO6/RBQkQZvSi8I/pL4BoGw/XPkh4XigPmsUgh0626AjRsgxHTkUThsG2T/sIlzdTsp52kSS1wAAAABJRU5ErkJggg==);
      }

.lc-menu .rc-menu__item--active {
      color: inherit;
      background-color: rgb(var(--lc-color-gray-lighter));
    }

.lc-menu .rc-menu__item .lc-icon:first-child {
      position: relative;
      right: 4px;
    }

.lc-menu .rc-menu__item .lc-icon:last-child {
      position: relative;
      left: 4px;
    }

/* purgecss end ignore */
